  /**/ }

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
  .header {
    &__catalog {
      &-btn {
        font-size: 0;
        width: 97px;
        span {
          margin-right: 0;
          vertical-align: 2px; } } }
    &__search {
      width: 294px; }
    &__links {
      &-li {
        width: 81px; } } }
  .main-catalog {
    &__block {
      &-img {
        &[src*='-2x'] {
          right: -90px; } } } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
  .footer {
    &__navigation {
      &-title {
        font-size: rem(14); } } }
  .header {
    &__catalog {
      &-btn {
        font-size: 0;
        width: 50px; } }
    &__search {
      width: 220px; }
    &__links {
      &-li {
        width: 62px; }
      &-a {
        font-size: rem(9); } }
    &__bottom {
      &-wrapper {
        &>div {
          &+div {
            margin-left: 22px; } } } } }
  .main-catalog {
    &__block {
      height: 180px;
      &-img {
        &[src*='-2x.png'] {
          height: 153px;
          right: -66px; }
        &[src*='-2.png'] {
          height: 140px; }
        &[src*='-3.png'] {
          height: 128px; }
        &[src*='-4.png'] {
          height: 134px; }
        &[src*='-5.png'] {
          height: 160px; } }
      &-title {
        font-size: rem(20);
        line-height: 25px;
        top: 20px;
        &::before {
          top: 3px; } }
      &-list {
        top: 100px;
        &>span {
          &:not(:last-child) {
            margin-bottom: 10px; } } }
      svg {
        width: 174px;
        height: 186px;
        right: -38px; } } }
  .main-tab {
    &__tab {
      font-size: rem(16);
      &>li {
        &:not(:last-child) {
          margin-right: 40px; }
        &>a {
          height: 40px;
          line-height: 40px; } } } } }

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {

  .animated {
    transition-property: none !important;
    transform: none !important;
    animation: none !important; }
  .footer {
    &__navigation {
      margin: 0 auto;
      max-width: 345px;
      &-title {
        font-size: rem(18);
        color: #eee; }
      &-block {
        float: none;
        width: 100%;
        padding: 0;
        &.is-active {
          .footer__navigation-list {
            display: block; } } }
      &-list {
        display: none;
        margin-bottom: 25px; } }
    &__card, &__copyright {
      text-align: center;
      float: none; } }
  .header {
    &__top {
      &-left, &-right {
        display: none; } }
    &__logo {
      position: absolute;
      top: 0;
      margin-top: 9px;
      margin-left: 60px;
      img {
        width: 100px;
        height: 24px;
        background-image: url('../img/header-logo-mobile.png'); } }
    &__links {
      position: absolute;
      right: 50px;
      margin: -40px 0 0 0;
      &-li {
        width: auto;
        margin-right: 24px;
        &:last-child {
          margin-right: 0; }
        &.is-active {
          background: none;
          &>a {
            background: none !important; } } }
      &-a {
        height: 40px;
        padding-top: 9px;
        background: none !important;
        svg, img {
          width: 22px;
          height: 22px; }
        span {
          display: none; } }
      &-popup {
        top: 36px;
        &--user {
          left: inherit;
          right: 0;
          &::before {
            left: inherit;
            right: 41px; }
          &::after {
            left: inherit;
            right: 38px; } } } }
    &__bottom {
      &-wrapper {
        &>div {
          &:nth-child(2), &:last-child {
            margin-left: 0; }
          &:nth-child(3) {
            width: 100%;
            margin-left: 15px; } } } }
    &__search {
      width: 100%; } }
  .content {
    padding: 30px 0; }
  .main-catalog {
    margin: 0 auto;
    max-width: 357px;
    padding-bottom: 15px;
    &>div {
      padding: 0; }
    &__block {
      height: 115px;
      margin-bottom: 15px;
      &-list {
        display: none; }
      &-img {
        &[src*='-2x.png'] {
          height: 95px;
          right: -90px; }
        &[src*='-2.png'] {
          height: 90px; }
        &[src*='-3.png'] {
          height: 82px; }
        &[src*='-4.png'] {
          height: 94px; }
        &[src*='-5.png'] {
          height: 95px; } }
      svg {
        width: 111px;
        height: 119px;
        right: -24px; }
      &-title {
        font-size: rem(26);
        line-height: 30px;
        top: 16px;
        &::before {
          top: 6px; } } } }
  .main-tab {
    &__tab {
      font-size: rem(15);
      &>li {
        &:not(:last-child) {
          margin-right: 22px; } } }
    &__block {
      max-width: 350px;
      margin-left: auto;
      margin-right: auto; } } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {
  .footer {
    &__navigation {
      max-width: 190px;
      &-title {
        font-size: rem(16); } } }
  .header {
    &__logo {
      margin-left: 0; }
    &__links {
      right: 15px;
      &-popup {
        &-wrapper {
          width: 290px; }
        &--user {
          right: -58px; } } }
    &__catalog {
      &-img {
        display: none; }
      &-info {
        display: none; }
      &-popup {
        &-wrapper {
          width: 290px; } }
      &-body {
        width: 100%;
        box-shadow: none; } }
    &__cart {
      &-row {
        padding-left: 45px;
        &-img {
          display: none; } } } }
  .main-catalog {
    &__block {
      &-title {
        font-size: rem(20);
        line-height: 25px;
        &::before {
          top: 3px; } } } }
  .main-slider {
    margin-bottom: 55px;
    .slick-dots {
      bottom: -24px; } }
  .main-tab {
    &__tab {
      font-size: rem(10);
      &>li {
        &:not(:last-child) {
          margin-right: 10px; } } } } }

